:root {
	--black: #231f20;
	--yellow: #f0e7d8;
	--red: #a63a50;
	--white: #f3f5f7;
	font-size: 16px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	background-color: var(--yellow);
	scroll-behavior: smooth;
	overflow-x: hidden;
}

.external-link {
	color: var(--yellow);
	text-decoration: underline;
	text-decoration-color: var(--red);
	transition: 0.1s;
	font-family: "Roboto Mono", monospace;
}

.external-link:hover {
	background-color: var(--red);
}

.about h1,
.home-cont h1,
.projects h1,
#contact h1 {
	font-family: "Playfair Display", serif;
	font-size: 4rem;
	font-weight: 600;
}

@media screen and (max-width: 876px) {
	.about h1,
	.home-cont h1,
	.projects h1,
	#contact h1 {
		font-size: 3rem;
	}
}
